<template>
    <div class="container">
        <div class="imageContainer">
          <div class="image" id="beer-picture" v-background="beer.image_url" ></div>
        </div>
        <div class="content">
          <div class="header">
            <h4>{{ beer.name }}</h4>
            <h6>{{ beer.tagline }}</h6>
          </div>
          <div class="footer">
            <router-link :to="`beer/${beer.id}`" >Details</router-link>
            <button v-if="!!userInfo.id" @click="showModal = true" > I'm having it</button>
          </div>
        </div>

        <modal v-if="showModal"  @close="showModal = false"  @submit="checkin()" >
          <h3 slot="header">You're going to check in to {{beer.name}}</h3>
          <div slot="body">
            <input type="textarea" v-model="text" />
            <star-rating :star-size="25" :increment=".25" v-model="rating" />
          </div>
        </modal>
    </div>
</template>

<script>
import { checkIn } from '../../database/queries.js'
import StarRating from 'vue-star-rating'
import Modal from '../containers/Modal.vue'

export default {
  props: {
    beer: {
      type: Object,
      default: () => {},
    },
  },
  components: {
    Modal,
    StarRating,
  },
  data() {
    return {
      showModal: false,
      text: '',
      rating: 0,
    }
  },
  methods: {
    async checkin() {
      const info = {
        beer: this.beer.id,
        rating: this.rating,
        text: this.text,
      }
      try {
        await this.$apollo.mutate({
          mutation: checkIn,
          variables: info,
        })
        this.showModal = false
        this.rating = 0
        this.text = ''
      } catch (err) {
        console.warn(err)
      }
    },
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo
    },
  },
}
</script>

